<template>
    <div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue;">返回</el-button>
		</div>
		<template v-if="details!=null && details!=''">
			<table>
				<tr>
					<td>客户号：</td><td class="creditPart">{{details.customerNo}}</td>
					<td>额度编号：</td><td class="creditPart">{{details.creditNum}}</td>
					<td>额度类型：</td><td class="creditPart">{{details.creditType}}</td>
					<td>合同号：</td><td class="creditPart">{{details.contractNo}}</td>
	
				</tr>	
				<tr>
					<td>当前生效额度：</td><td class="creditPart">{{details.effectiveCredit}}</td>
					<td>已使用额度：</td><td class="creditPart">{{details.usedCredit}}</td>
					<td>当前额度生效日期：</td><td class="creditPart">{{details.creditStartDate}}</td>
					<td>失效日期：</td><td class="creditPart">{{details.expirationDate}}</td>				
				</tr>
				<tr>
	
					<td>变更未对消授权金额：</td><td class="creditPart">{{details.noUsedAmount}}</td>
					<td>实时还款未入账金额：</td><td class="creditPart">{{details.payNoComingAmount}}</td>
				</tr>			
			</table>
		</template>	
	</div>	
</template>

<script>
	export default {
		data() {
			return {
					details:''
			}
		},
		methods: {
			//获取数据列表
			getParams:function(){
				if(this.$route.params !=null){
				   let customerNo = this.$route.params.customerNo;
					if(customerNo != null && customerNo!==""){
						sessionStorage.setItem('customerNo', customerNo);
					}
					let creditType = this.$route.params.creditType;
					if(creditType!= null && creditType !== ""){
						sessionStorage.setItem('creditType', creditType);
					}	
				    this.details = this.$route.params.param;
				}    
			},
			back:function(){
				history.go(-1);
			},

        },
	     mounted: function() {
		   this.getParams();
		}
 }
</script>
<style>
		.creditPart{
		color: blue;
		padding-top: 15px;
    	padding-bottom: 15px;
    	text-align:left;
	}
</style>


