<template>
<div>

	   <div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue">返回</el-button>
		</div>
		<template v-if="detail!=null && detail !='' && payPlanItem !=null && payPlanItem !='' ">
			<table>
				<tr>
					<td>贷款编号：</td><td class="payPlanPart">{{detail.loanNumber}}</td>
					<td>交易日期：</td><td class="payPlanPart">{{detail.transactionDate}}</td>
					<td>还款截止日期：</td><td class="payPlanPart">{{payPlanItem.repaymentDate}}</td>
					<td>应还本金：</td><td class="payPlanPart">{{payPlanItem.payAmount}}</td>
				</tr>
				<tr>
					<td>计划本金：</td><td class="payPlanPart">{{payPlanItem.planPrincipal}}</td>
					<td>剩余本金：</td><td class="payPlanPart">{{payPlanItem.residualPrincipal}}</td>
				    <td>计划利息：</td><td class="payPlanPart">{{payPlanItem.planInterest}}</td>
					<td>期数：</td><td class="payPlanPart">{{payPlanItem.periods}}</td>
	
				</tr>
			</table>
		</template>	
		<template v-if="payPlanItemList!=null">
			<div v-for="(item,index) in payPlanItemList">
			<p class="part2">期数:{{index+1}}期</p>
			<table>	
				<tr >
				    <td>状态：</td><td class="payPlanPart">{{payPlanItem.state}}</td>
					<td>本期延滞天数：</td><td class="payPlanPart">{{item.delayDays}}</td>
					<td>当期总金额：</td><td class="payPlanPart">{{item.totalAmount}}</td>
					<td>当期本金：</td><td class="payPlanPart">{{item.principal}}</td>
					<td>当期利息：</td><td class="payPlanPart">{{item.interest}}</td>
				</tr>
				<tr>
					<td>当期预期金额：</td><td class="payPlanPart">{{item.delayAmount}}</td>
					<td>当期罚息：</td><td class="payPlanPart">{{item.penalty}}</td>
					<td>本期开始日期：</td><td class="payPlanPart">{{item.startDate}}</td>
					<td>计划还款日期：</td><td class="payPlanPart">{{item.planPayDate}}</td>
				    <td>本期还款日期：</td><td class="payPlanPart">{{item.payDate}}</td>
				</tr>	
				<tr>
					<td>本期利率：</td><td class="payPlanPart">{{item.interestRate}}</td>
					<td>本期实还本金：</td><td class="payPlanPart">{{item.realPrincipal}}</td>
					<td>本期实还罚息：</td><td class="payPlanPart">{{item.realPenalty}}</td>	
					<td>本期结清标记：</td><td class="payPlanPart">{{item.flag}}</td>	
				</tr>
			</table>
			</div>
		</template>	
</div>		
</template>

<script>
	export default {
		data() {
			return {
					detail:'',
					payPlanItem: '',
					payPlanItemList:[],
			}
		},
		methods: {
			//获取数据列表
			getParams:function(){		
			if(this.$route.params.param != null){
				sessionStorage.setItem("ass",JSON.stringify(this.$route.params.param));
				let params = this.$route.params.param.payPlanInfo;
				this.detail = params;
				this.payPlanItem = this.$route.params.param.payPlanInfo.payPlanItemList[0];
				this.payPlanItemList = this.$route.params.param.payPlanInfo.payPlanItemList;
				}
			},
		    back:function(){
				history.go(-1);
			},

        },
	     mounted: function() {
		   this.getParams();
		}
 }	
</script>

<style>
	.part{
		/*background-color: cornflowerblue;*/
	}
	.payPlanPart{
		color: blue;
		padding-top: 15px;
    	padding-bottom: 15px;
    	text-align:left;
	}
	.part2{
		background-color: gainsboro;
		width: 4000px;
	}
</style>


