<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				<el-form-item label="贷款编号" prop="loanNumber">
			    <el-input v-model="filters.loanNumber"  placeholder="请输入贷款编号"></el-input>
				</el-form-item>
				<el-form-item label="贷款日期" prop="time">
					    <el-date-picker v-model="filters.time" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="loans" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="loanNumber" label="借款编号" width="290" >
			</el-table-column>
			<el-table-column prop="loanDate" label="借款日期" width="120" >
			</el-table-column>
			<el-table-column prop="loanAmount" label="借款金额" width="100" >
			</el-table-column>
			<el-table-column prop="rate" label="利率" width="120" >
			</el-table-column>
			<el-table-column prop="periods" label="期数" width="100" >
			</el-table-column>
	        <el-table-column prop="accountState" label="账户状态" width="150" >
			</el-table-column>
			<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryAssertDetail(scope.$index)">详情</el-button>
                   <!--<router-link :to="{ name: '资产详情', params: { this.assertData[scope.$index] }}">详情</router-link>-->
                 </template>  
			</el-table-column>
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryAssert} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					loanNumber: '',
					time: '',
				},
				loans: [],
				assertData:[],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.loans = [];
				let loanDate = (!this.filters.time || this.filters.time == '') ? '' : util.formatDate.format(new Date(this.filters.time), 'yyyy/MM/dd');
				let para =  JSON.stringify({
					loanNumber: this.filters.loanNumber,
					date: loanDate,
					dataType:'8'
				});
				this.listLoading = true;
				requestQueryAssert(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
						var objArray=new Array();
					    dataObj.forEach(function(item ,index){					    	
					      objArray.push(item.loanDetailInfo);
					    })		
					    this.loans = objArray;
					    this.assertData = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.loans.push(dataObj.loanDetailInfo);
					    	this.assertData.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//查看贷款明细详情
			queryAssertDetail: function(index){	
			  let loanDate = (!this.filters.time || this.filters.time == '') ? '' : util.formatDate.format(new Date(this.filters.time), 'yyyy/MM/dd');
              let loanNumber = this.filters.loanNumber;
              let arr = this.assertData;
				this.$router.push({
				   name: '资产详情',
				   params:{param:this.assertData[index],loanData:loanDate,loanNumber:loanNumber},
			    });
			},
			paramsInit:function(){
				let loanDate = sessionStorage.getItem('loanDate');
				let loanNumber = sessionStorage.getItem('loanNumber');

					if(loanDate !=null &&loanDate!==""){
						this.filters.time = loanDate;
					}
					if(loanNumber !=null &&loanNumber!==""){
						this.filters.loanNumber = loanNumber;
					}
					sessionStorage.removeItem("loanDate");
					sessionStorage.removeItem("loanNumber");
					if(loanDate != null || loanNumber !=null){
							this.loans = [];
							this.getInfo();
					}				
			}
	
   },
   	     mounted: function() {
		   this.paramsInit();
		}
 }	
</script>



