<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				<el-form-item label="借款编号" prop="loanNumber">
			    <el-input v-model="filters.loanNumber"  placeholder="请输入贷款编号"></el-input>
				</el-form-item>
				<el-form-item label="发生日期" prop="time">
					    <el-date-picker v-model="filters.time" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="acctFlows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="loanNumber" label="贷款编号" width="300" >
