			<el-table-column prop="happenDate" label="发生日期" width="120" >
			</el-table-column>
			<el-table-column prop="currency" label="币种" width="80" >
			</el-table-column>
			<el-table-column prop="businessCode" label="交易码" width="80" >
			</el-table-column>
			<el-table-column prop="businessDesc" label="交易描述" width="200" >
			</el-table-column>
	        <el-table-column prop="loanFlag" label="借贷标记" width="100" >
			</el-table-column>
			 <el-table-column prop="businessAmount" label="入账金额" width="100" >
			</el-table-column>
			 <el-table-column prop="branchNo" label="分支行号" width="100" >
			</el-table-column>
			 <el-table-column prop="productNo" label="产品号" width="180" >
			</el-table-column>
			 <el-table-column prop="subject" label="科目" width="80" >
			</el-table-column>			
			<!--<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryAcctFlowDetail(scope.$index)">详情</el-button>
