          <!--       </template>  
			</el-table-column>-->
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryAcctFlow} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					loanNumber: '',
					time: ''				
				},
				acctFlows: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.acctFlows = [];
				let happenDate = (!this.filters.time || this.filters.time == '') ? '' : util.formatDate.format(new Date(this.filters.time), 'yyyy/MM/dd');
				let para =  JSON.stringify({
					loanNumber: this.filters.loanNumber,
					date: happenDate
				});
				this.listLoading = true;
				requestQueryAcctFlow(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
					    this.acctFlows = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.acctFlows.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//查看贷款明细详情
