<template>
    <div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue">返回</el-button>
		</div>
		<template v-if="details!=null">
			<div v-for="(item,index) in details">
			<table>
				<tr>
					<td>贷款编号：</td><td class="specialPart">{{item.loanNumber}}</td>
					<td>发生日期：</td><td class="specialPart">{{item.happenDate}}</td>
					<td>特殊交易类型：</td><td class="specialPart">{{item.businessType}}</td>
					<td>特殊交易金额：</td><td class="specialPart">{{item.businessAmount}}</td>
				<tr>
					<td>明细信息：</td><td class="specialPart">{{item.detailInfo}}</td>
					<td>变更期次：</td><td class="specialPart">{{item.changePeriod}}</td>
					<td>还款计划变更标识：</td><td class="specialPart">{{item.payPlanChangeFlag}}</td>
				</tr>	
			</table>
			</div>
		</template>		
	</div>	
</template>

<script>
	export default {
		data() {
			return {
					details:[]
			}
		},
		methods: {
			//获取数据列表
			getParams:function(){
			sessionStorage.setItem("ass",JSON.stringify(this.$route.params.param));
			let params = this.$route.params.param.specialBusinessListInfo;
			this.details = params;
			},
			back:function(){
				history.go(-1);
			},

        },
	     mounted: function() {
		   this.getParams();
		}
 }
</script>
<style >
	.specialPart{
		color: blue;
		padding-top: 15px;
    	padding-bottom: 15px;
    	text-align:left;
	}
</style>


