<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				<el-form-item label="客户号" prop="customerNo">
			    <el-input v-model="filters.customerNo"  placeholder="请输入客户号"></el-input>
				</el-form-item>
				<el-form-item label="额度类型" prop="creditType">
					    <el-input v-model="filters.creditType"  placeholder="请输入额度类型"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="credits" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="customerNo" label="客户号" width="200" >
			</el-table-column>
			<el-table-column prop="creditNum" label="额度编号" width="200" >
			</el-table-column>
			<el-table-column prop="creditType" label="额度类型" width="100" >
			</el-table-column>
			<el-table-column prop="effectiveCredit" label="当前生效额度" width="150" >
			</el-table-column>
			<el-table-column prop="usedCredit" label="已使用额度" width="150" >
			</el-table-column>
	        <el-table-column prop="expirationDate" label="失效日期" width="150" >
			</el-table-column>
			<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryCreditDetail(scope.$index)">详情</el-button>
                   <!--<router-link :to="{ name: '资产详情', params: { this.assertData[scope.$index] }}">详情</router-link>-->
                 </template>  
			</el-table-column>
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryCredit} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					customerNo: '',
					creditType: ''				
				},
				credits: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.credits = [];
				let para =  JSON.stringify({
					customerNo: this.filters.customerNo,
					creditType: this.filters.creditType
				});
				this.listLoading = true;
				requestQueryCredit(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
					    let dataObj = JSON.parse(data);
					    this.credits = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.credits.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//查看贷款明细详情
			queryCreditDetail: function(index){	
			  let customerNo = this.filters.customerNo
              let creditType = this.filters.creditType;
				this.$router.push({
				   name: '授信详情',
				   params:{param:this.credits[index],customerNo:customerNo,creditType:creditType},
			    });
			},
			paramsInit:function(){
				let customerNo = sessionStorage.getItem('customerNo');
				let creditType = sessionStorage.getItem('creditType');

					if(customerNo !=null &&customerNo!==""){
						this.filters.customerNo = customerNo;
					}
					if(creditType !=null &&creditType!==""){
						this.filters.creditType = creditType;
					}
					sessionStorage.removeItem("customerNo");
					sessionStorage.removeItem("creditType");
					if(customerNo != null || creditType !=null){
							this.credits = [];
							this.getInfo();
					}			
			}
		
   },
   	     mounted: function() {
		   this.paramsInit();
		}
 }	
</script>
<style scoped>

</style>


