<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				
				<el-form-item label="备付金账号" prop="bfjNo">
			    <el-input v-model="filters.bfjNo"  placeholder="请输入备付金账号"></el-input>
				</el-form-item>
				<el-form-item label="日期" prop="transactionDate">
					    <el-date-picker v-model="filters.transactionDate" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="bfjSums" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="bfjNo" label="备付金账号" width="320" >
			</el-table-column>
			<el-table-column prop="transactionDate" label="日期" width="320" >
			</el-table-column>
			<el-table-column prop="amountBalance" label="账号金额" width="320" >
			</el-table-column>	
			<!--<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryAcctFlowDetail(scope.$index)">详情</el-button>
                   <!--<router-link :to="{ name: '资产详情', params: { this.assertData[scope.$index] }}">详情</router-link>-->
          <!--       </template>  
			</el-table-column>-->
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryBfjSum} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					transactionDate: '',
					bfjNo:''
				},
				bfjSums: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				 this.bfjSums = [];
				let transactionDate = (!this.filters.transactionDate || this.filters.transactionDate == '') ? '' : util.formatDate.format(new Date(this.filters.transactionDate), 'yyyy/MM/dd');
				let para =  JSON.stringify({
					transactionDate: transactionDate,
					bfjNo:this.filters.bfjNo
				});
				this.listLoading = true;
				requestQueryBfjSum(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
					    this.bfjSums = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.bfjSums.push(data);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},	
   },
   	     mounted: function() {
//		   this.paramsInit();
		}
 }	
</script>
<style scoped>

</style>


