import axios from 'axios';

let base= '/xwfabric_web/fabric';
//设置全局参数
//axios.defaults.timeout = 10000;
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';//appliction/x-www-form-urlencoded

/**
 * 联盟链平台
 */
export const requestLogin = params => { return axios.get(`/xwfabric_web/static/login.json`, params).then(res => res.data); };
export const requestQueryAssert = params => { return axios.post(`${base}/queryAssertData.do`, params).then(res => res.data); };
export const requestQueryCredit = params => { return axios.post(`${base}/queryCreditData.do`, params).then(res => res.data); };
export const requestQueryAcctFlow = params => { return axios.post(`${base}/queryAcctFlowData.do`, params).then(res => res.data); };
export const requestQueryBfjFlow = params => { return axios.post(`${base}/queryBfjFlowData.do`, params).then(res => res.data); };
export const requestQueryAcct = params => { return axios.post(`${base}/queryAcctData.do`, params).then(res => res.data); };
export const requestQueryBfjSum = params => { return axios.post(`${base}/queryBfjSumData.do`, params).then(res => res.data); };
export const requestQueryFee = params => { return axios.post(`${base}/queryFeeData.do`, params).then(res => res.data); };
export const requestQueryRevTrans = params => { return axios.post(`${base}/queryRevTransData.do`, params).then(res => res.data); };




