<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				<el-form-item label="交易流水号" prop="transactionSerialNo">
			    <el-input v-model="filters.transactionSerialNo"  placeholder="请输入交易流水号"></el-input>
				</el-form-item>
				<el-form-item label="实际扣费日期" prop="actualTransactionDate">
					    <el-date-picker v-model="filters.actualTransactionDate" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="fees" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="dueDate" label="应扣费日期" width="120" >
			</el-table-column>
			<el-table-column prop="payableAmount" label="应扣费金额" width="120" >
			</el-table-column>
			<el-table-column prop="actualTransactionDate" label="实际扣费日期" width="150" >
			</el-table-column>
			<el-table-column prop="actualDeductionAmount" label="实际扣费金额" width="150" >
			</el-table-column>
			<el-table-column prop="deductionStatus" label="扣费状态" width="100" >
			</el-table-column>
	        <el-table-column prop="transactionSerialNo" label="交易流水号" width="200" >
			</el-table-column>
			 <el-table-column prop="deductionRule" label="扣费规则" width="100" >
			</el-table-column>
			 <el-table-column prop="deductionRuleName" label="扣费规则名称" width="150" >
			</el-table-column>
			 <el-table-column prop="deductionRuleCode" label="扣费规则编码" width="150" >
			</el-table-column>	
			 <el-table-column prop="basedOn" label="计费基础" width="100" >
			</el-table-column>	
			 <el-table-column prop="rate" label="费率" width="100" >
			</el-table-column>				
			<!--<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryAcctFlowDetail(scope.$index)">详情</el-button>
                   <!--<router-link :to="{ name: '资产详情', params: { this.assertData[scope.$index] }}">详情</router-link>-->
          <!--       </template>  
			</el-table-column>-->
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryFee} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					actualTransactionDate: '',
					transactionSerialNo:''
				},
				fees: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.fees = [];
				let transactionSerialNo = this.filters.transactionSerialNo;
				let actualTransactionDate = (!this.filters.actualTransactionDate || this.filters.actualTransactionDate == '') ? '' : util.formatDate.format(new Date(this.filters.actualTransactionDate), 'yyyy/MM/dd');
				
				let para =  JSON.stringify({
					transactionSerialNo: transactionSerialNo,
					actualTransactionDate:actualTransactionDate
				});
				this.listLoading = true;
				requestQueryFee(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
					    this.fees = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.fees.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},	
   },
   	     mounted: function() {
//		   this.paramsInit();
		}
 }	
</script>
<style scoped>

</style>


