<template>
<div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue">返回</el-button>
			<el-button size="middle" @click="payPlan()" class="but2" style="background-color: aliceblue;">还款计划</el-button>
			<el-button size="middle" @click="payLog()" class="but3" style="background-color: aliceblue">还款明细</el-button>
			<el-button size="middle" @click="specialBusiness()" class="but4" style="background-color: aliceblue">特殊交易</el-button>
			<el-button size="middle" @click="acctFlow()" class="but5" style="background-color: aliceblue" v-loading="listLoading">会计分录</el-button>

		</div>
		<template v-if="loanDetail!=null && loanDetail!=''">
			<table class="tab1">
				<tr>
					<td class="title1">贷款明细</td>
				</tr>
				<tr>
					<td>贷款编号：</td><td class="loanDetailPart">{{loanDetail.loanNumber}}</td>
					<td>借款日期：</td><td class="loanDetailPart">{{loanDetail.loanDate}}</td>
					<td>借款结束日期：</td><td class="loanDetailPart">{{loanDetail.loanEndDate}}</td>
					<td>借款用途：</td><td class="loanDetailPart">{{loanDetail.loanPurpose}}</td>
				</tr>
				<tr>
					<td>借款类型：</td><td class="loanDetailPart">{{loanDetail.loanType}}</td>
					<td>借款人类型：</td><td class="loanDetailPart">{{loanDetail.borrowerType}}</td>
				    <td>利率：</td><td class="loanDetailPart">{{loanDetail.rate}}</td>
					<td>利率类型：</td><td class="loanDetailPart">{{loanDetail.rateType}}</td>
				</tr>	
				<tr >
					<td>产品名称：</td><td class="loanDetailPart">{{loanDetail.productName}}</td>
					<td>产品编号：</td><td class="loanDetailPart">{{loanDetail.productNo}}</td>
					<td>交易起息日：</td><td class="loanDetailPart">{{loanDetail.transactionDate}}</td>
					<td>停息标志：</td><td class="loanDetailPart">{{loanDetail.stopInterestFlag}}</td>
				</tr>
				<tr>
					<td>还款方式：</td><td class="loanDetailPart">{{loanDetail.payMethod}}</td>
					<td>还款周期：</td><td class="loanDetailPart">{{loanDetail.repaymentPeriod}}</td>
					<td>首次还款日：</td><td class="loanDetailPart">{{loanDetail.firstPayDate}}</td>
					<td>最大延滞天数：</td><td class="loanDetailPart">{{loanDetail.maxDelayDays}}</td>
				</tr>	
				<tr>
					<td>已还期数：</td><td class="loanDetailPart">{{loanDetail.repaidPeriod}}</td>
					<td>结清日：</td><td class="loanDetailPart">{{loanDetail.settleOnDate}}</td>
					<td>交易流水号：</td><td class="loanDetailPart">{{loanDetail.transactionFlowNo}}</td>
					<td>期数：</td><td class="loanDetailPart">{{loanDetail.periods}}</td>				
				</tr>
				<tr>
					<td>客户号：</td><td class="loanDetailPart">{{loanDetail.customerNo}}</td>
					<td>合同号：</td><td class="loanDetailPart">{{loanDetail.contractNo}}</td>
					<td>担保方式：</td><td class="loanDetailPart">{{loanDetail.guaranteeMethod}}</td>
					<td>客户姓名：</td><td class="loanDetailPart">{{loanDetail.customerName}}</td>
				</tr>
				<tr>
					<td>账户状态：</td><td class="loanDetailPart">{{loanDetail.accountState}}</td>
					<td>借款金额：</td><td class="loanDetailPart">{{loanDetail.loanAmount}}</td>
					<td>正常余额：</td><td class="loanDetailPart">{{loanDetail.normalBalance}}</td>
					<td>逾期余额：</td><td class="loanDetailPart">{{loanDetail.delayBalance}}</td>
				</tr>
				<tr>
					<td>罚息利率：</td><td class="loanDetailPart">{{loanDetail.penaltyRate}}</td>
					<td>罚息金额：</td><td class="loanDetailPart">{{loanDetail.penaltyAmount}}</td>
					<td>欠息金额：</td><td class="loanDetailPart">{{loanDetail.debitInterestAmount}}</td>
					<td>收款人银行账号：</td><td class="loanDetailPart">{{loanDetail.receiverBankNo}}</td>
				</tr>
				<tr>
					<td>收款人账户银行：</td><td class="loanDetailPart">{{loanDetail.receiverBankType}}</td>
					<td>收款人姓名：</td><td class="loanDetailPart">{{loanDetail.receiverName}}</td>
				</tr>
			</table>
		</template>
		<template v-if="customerDetail!=null && customerDetail!=''">
			<table class="tab1">
				<tr>
					<td class="title1">客户信息</td>
				</tr>
				<tr>
					<td>客户号：</td><td class="loanDetailPart">{{customerDetail.customerNo}}</td>
					<td>姓名</td><td class="loanDetailPart">{{customerDetail.name}}</td>
					<td>性别：</td><td class="loanDetailPart">{{customerDetail.sex}}</td>
					<td>证件类型：</td><td class="loanDetailPart">{{customerDetail.certificateType}}</td>
				</tr>
				<tr>
				   <td>居住地址：</td><td class="loanDetailPart">{{customerDetail.address}}</td>
					<td>证件号码：</td><td class="loanDetailPart">{{customerDetail.certificateNum}}</td>
					<td>出生年月：</td><td class="loanDetailPart">{{customerDetail.birthday}}</td>
				    <td>手机号码：</td><td class="loanDetailPart">{{customerDetail.phone}}</td>
				</tr>	
				<tr >
					<td>居住区：</td><td class="loanDetailPart">{{customerDetail.district}}</td>
					<td>居住市：</td><td class="loanDetailPart">{{customerDetail.city}}</td>
					<td>居住省：</td><td class="loanDetailPart">{{customerDetail.province}}</td>
					<td>联系人：</td><td class="loanDetailPart">{{customerDetail.linkMan}}</td>
				</tr>
				<tr>
					<td>联系人电话：</td><td class="loanDetailPart">{{customerDetail.linkManPhone}}</td>
					<td>联系人关系：</td><td class="loanDetailPart">{{customerDetail.linkManRelation}}</td>
					<td>公司名称：</td><td class="loanDetailPart">{{customerDetail.companyName}}</td>
					<td>公司类型：</td><td class="loanDetailPart">{{customerDetail.companyType}}</td>
				</tr>	
				<tr >
					<td>公司地址：</td><td class="loanDetailPart">{{customerDetail.companyAddress}}</td>
					<td>公司地址-区：</td><td class="loanDetailPart">{{customerDetail.companyDistrict}}</td>
					<td>公司地址-市：</td><td class="loanDetailPart">{{customerDetail.companyCity}}</td>
					<td>公司地址-省：</td><td class="loanDetailPart">{{customerDetail.companyProvince}}</td>				
				</tr>
				<tr>
					<td>家庭地址</td><td class="loanDetailPart">{{customerDetail.homeAddress}}</td>
					<td>居住地址邮编：</td><td class="loanDetailPart">{{customerDetail.addressCode}}</td>
					<td>通讯地址邮编：</td><td class="loanDetailPart">{{customerDetail.communicationCode}}</td>
					<td>公司地址邮编：</td><td class="loanDetailPart">{{customerDetail.companyCode}}</td>
				</tr>
				<tr>
					<td>婚姻状况：</td><td class="loanDetailPart">{{customerDetail.maritaStatus}}</td>
					<td>学历：</td><td class="loanDetailPart">{{customerDetail.education}}</td>
					<td>个人收入：</td><td class="loanDetailPart">{{customerDetail.income}}</td>
					<td>住宅电话：</td><td class="loanDetailPart">{{customerDetail.mobile}}</td>
				</tr>
				<tr>
					<td>职业：</td><td class="loanDetailPart">{{customerDetail.job}}</td>
					<td>本人职务：</td><td class="loanDetailPart">{{customerDetail.jobLeval}}</td>
					<td>本人职称：</td><td class="loanDetailPart">{{customerDetail.jobTitle}}</td>
					<td>更新标识：</td><td class="loanDetailPart">{{customerDetail.updateFlag}}</td>
				</tr>
				<tr>
					<td>身份证地址：</td><td class="loanDetailPart">{{customerDetail.receiverBankType}}</td>
					<td>身份证有效期起：</td><td class="loanDetailPart">{{customerDetail.cardStartDate}}</td>
				    <td>身份证有效期止：</td><td class="loanDetailPart">{{customerDetail.cardEndDate}}</td>				
				</tr>
			</table>
		</template>
	</div>	
</template>

<script>
    import util from '../../common/js/util'
	import {requestQueryAcctFlow} from '../../api/api';
	export default {
		data() {
			return {
					assertData:'',
					loanDetail:'',
					customerDetail:'',
					payPlanDetail:'',
					payLogListDetail:[],
					specialBusinessListDetail:[],
					acctFlows:[],
				    listLoading: false,
			}
		},
		methods: {
			//获取数据列表
			getParams:function(){
				if(this.$route.params.param != null){
					//资产
					this.assertData = this.$route.params.param;
					//贷款明细
					this.loanDetail = this.$route.params.param.loanDetailInfo;
					//客户信息
					this.customerDetail = this.$route.params.param.customerInfo;
					//还款计划
					this.payPlanDetail = this.$route.params.param.payPlanInfo;
					//还款明细
					this.payLogListDetail = this.$route.params.param.payLogListInfo;
					//特殊交易
					this.specialBusinessListDetail = this.$route.params.param.specialBusinessListInfo;
					
				    let loanDate = this.$route.params.loanData;
					if(loanDate != null && loanDate!==""){
						sessionStorage.setItem('loanDate', loanDate);
					}
					let loanNumber = this.$route.params.loanNumber;
					if(loanNumber!= null && loanNumber !== ""){
						sessionStorage.setItem('loanNumber', loanNumber);
					}
				}
			},
			back:function(){
				history.go(-1);
			},
			payPlan:function(){
				this.$router.push({
				   name: '还款计划详情',
				   params:{param:this.assertData},
			     });
			     
			},
			payLog:function(){
				this.$router.push({
				   name: '还款明细详情',
				   params:{param:this.assertData},
			     });
			},
			specialBusiness:function(){
				this.$router.push({
				   name: '特殊交易',
				   params:{param:this.assertData},
			     });
				
			},
			acctFlow:function(){
				let loanNumber = '';
				if(this.loanDetail  != null){
					loanNumber = this.loanDetail .loanNumber
				}
				let para =  JSON.stringify({
					loanNumber: loanNumber,
				});
				this.listLoading = true;
				requestQueryAcctFlow(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);	
					    this.acctFlows = dataObj;
					    this.$router.push({
						   name: '会计分录详情',
						   params:{param:this.assertData,acctparam:this.acctFlows},
			     });
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.acctFlows.push(dataObj);
					    	this.$router.push({
							   name: '会计分录详情',
							   params:{param:this.assertData,acctparam:this.acctFlows},
						     });
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
					 console.log(error)
					 this.listLoading = false;
				});				
			},

        },
	     mounted: function() {
	        if(sessionStorage.getItem("ass") != null && sessionStorage.getItem("ass")!="undefined" && sessionStorage.getItem("ass")!=undefined ){
	        	this.assertData = JSON.parse(sessionStorage.getItem("ass"));
	        	this.loanDetail = JSON.parse(sessionStorage.getItem("ass")).loanDetailInfo;
				//客户信息
				this.customerDetail = JSON.parse(sessionStorage.getItem("ass")).customerInfo;
				//还款计划
				this.payPlanDetail = JSON.parse(sessionStorage.getItem("ass")).payPlanInfo;
				//还款明细
				this.payLogListDetail = JSON.parse(sessionStorage.getItem("ass")).payLogListInfo;
				//特殊交易
				this.specialBusinessListDetail = JSON.parse(sessionStorage.getItem("ass")).specialBusinessListInfo;
				sessionStorage.removeItem("ass");
	        }else{
	        	this.getParams();
	        }

		}
 }	
</script>

<style>
	.tab1{
		margin-top: 15px;
		/*background-color: gainsboro;*/
	}
	.title1{
		font-size: 18px;
		color: blue;
	}
   .loanDetailPart{
		padding-top: 5px;
    	padding-bottom: 5px;
    	color:blue;
    	text-align:left;
	}
	.but1{
		margin-right: 100px;
	}
	.but2{
		margin-right: 60px;
	}
	.but3{
		margin-right: 60px;
	}
	.but4{
		margin-right: 60px;
	}
</style>


