<template>
<div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue">返回</el-button>
		</div>
		<template v-if="details!=null && details.length>0">
			<div v-for="(item,index) in details">
			<table>
				<tr>
					<td>贷款编号：</td><td class="payLogPart">{{item.loanNumber}}</td>
					<td>交易流水号：</td><td class="payLogPart">{{item.transactionSerialNo}}</td>
					<td>交易日期：</td><td class="payLogPart">{{item.transactionDate}}</td>
					<td>交易本金：</td><td class="payLogPart">{{item.transactionPrincipal}}</td>
				<tr>
					<td>入账时间：</td><td class="payLogPart">{{item.entryDate}}</td>
					<td>入账金额：</td><td class="payLogPart">{{item.entryAmount}}</td>
					<td>交易费用：</td><td class="payLogPart">{{item.transactionCost}}</td>
				    <td>交易利息：</td><td class="payLogPart">{{item.transactionInterest}}</td>
	
				</tr>	
				<tr >
					<td>交易罚息：</td><td class="payLogPart">{{item.transactionPenalty}}</td>
					<td>自动还款标识：</td><td class="payLogPart">{{item.repayFlag}}</td>
	                <td>第几期：</td><td class="payLogPart">{{item.period}}</td>
				</tr>	
			</table>
			</div>
		</template>	
	</div>	
</template>

<script>
	export default {
		data() {
			return {
					details:[]
			}
		},
		methods: {
			//获取数据列表
			getParams:function(){
			  if(this.$route.params.param != null){
				sessionStorage.setItem("ass",JSON.stringify(this.$route.params.param));
				let params = this.$route.params.param.payLogListInfo;
				this.details = params;
			   }		
				},
			back:function(){
				history.go(-1);
			},

        },
	     mounted: function() {
		   this.getParams();
		}
 }	
</script>

<style>
	.payLogPart{
		color: blue;
		padding-top: 15px;
    	padding-bottom: 15px;
    	text-align:left;
	}
</style>


