import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'

import queryBlockChain from './views/queryChain/queryBlockChain.vue'

import queryLoanDetailInfo from './views/queryChain/queryLoanDetailInfo.vue'
import queryAssertDetail from './views/queryChain/queryAssertDetail.vue'
import queryPayPlanDetailInfo from './views/queryChain/queryPayPlanDetailInfo.vue'
import queryPayLogDetailsInfo from './views/queryChain/queryPayLogDetailsInfo.vue'
import querySpecialBusinessDetail from './views/queryChain/querySpecialBusinessDetail.vue'
import queryCreditDetail from './views/queryChain/queryCreditDetail.vue'
import queryCreditInfo from './views/queryChain/queryCreditInfo.vue'
import queryAcctFlowDetail from './views/queryChain/queryAcctFlowDetail.vue'

import queryAcctFlowInfo from './views/queryChain/queryAcctFlowInfo.vue'
import queryBfjFlowInfo from './views/queryChain/queryBfjFlowInfo.vue'
import queryAcctInfo from './views/queryChain/queryAcctInfo.vue'
import queryAcctDetail from './views/queryChain/queryAcctDetail.vue'
import queryBfjSumInfo from './views/queryChain/queryBfjSumInfo.vue'
import queryFeeInfo from './views/queryChain/queryFeeInfo.vue'
import queryRevTransInfo from './views/queryChain/queryRevTransInfo.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    {
        path: '/',
        component: Home,
        name: '区块链查询',
        iconCls: 'el-icon-message',//图标样式class
        children: [
//          { path: '/main', component: Main, name: '主页', hidden: true },
            { path: '/queryBlockChain', component: queryBlockChain, name: '区块信息查询' },          
        ]
    },
        {
        path: '/',
        component: Home,
        name: '资产信息',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            { path: '/queryLoanDetailInfo', component: queryLoanDetailInfo, name: '贷款信息' }, 
            { path: '/queryCreditInfo', component: queryCreditInfo, name: '授信信息' },
            {path:'/queryAssertDetail',component: queryAssertDetail, name:"资产详情" ,hidden:true},
            {path:'/queryPayPlanDetailInfo',component: queryPayPlanDetailInfo, name:"还款计划详情" ,hidden:true},
            {path:'/queryPayLogDetailsInfo',component: queryPayLogDetailsInfo, name:"还款明细详情" ,hidden:true},
            {path:'/querySpecialBusinessDetail',component: querySpecialBusinessDetail, name:"特殊交易" ,hidden:true},
            {path:'/queryCreditDetail',component: queryCreditDetail, name:"授信详情" ,hidden:true},
            {path:'/queryAcctFlowDetail',component: queryAcctFlowDetail, name:"会计分录详情" ,hidden:true},
        ]
    },
            {
        path: '/',
        component: Home,
        name: '对账信息',
        iconCls: 'el-icon-message',//图标样式class
        children: [
            { path: '/queryAcctFlowInfo', component: queryAcctFlowInfo, name: '会计分录' }, 
            { path: '/queryBfjFlowInfo', component: queryBfjFlowInfo, name: '备付金流水' },
            {path:'/queryAcctInfo',component: queryAcctInfo, name:"日总额台账" },
            {path:'/queryAcctDetail',component: queryAcctDetail, name:"日总额台账详情" ,hidden:true},
            {path:'/queryBfjSumInfo',component: queryBfjSumInfo, name:"备付金日总余额"},
            {path:'/queryFeeInfo',component: queryFeeInfo, name:"扣费明细"},
            {path:'/queryRevTransInfo',component: queryRevTransInfo, name:"冲正交易"},
        ]
    },
    
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }    
];

export default routes;


