<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				<el-form-item label="对账日期" prop="reconciliationDate">
					    <el-date-picker v-model="filters.reconciliationDate" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="accts" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="reconciliationDate" label="对账日期" width="120" >
			</el-table-column>
			<el-table-column prop="lastLoanPrincipalBalance" label="上日贷款本金余额" width="180" >
			</el-table-column>
			<el-table-column prop="dayIssuePrincipalTotal" label="当日发放本金总额" width="180" >
			</el-table-column>
			<el-table-column prop="dayRePrincipalTotal" label="当日回收本金总额" width="180" >
			</el-table-column>
			<el-table-column prop="dayLoanPrincipalTotal" label="当日贷款本金余额" width="180" >
			</el-table-column>	
			<<el-table-column label="操作" width="120">
				<template scope="scope">
					<el-button size="small" @click="queryAcctDetail(scope.$index)">详情</el-button>
                 </template>  
			</el-table-column>
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryAcct} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					reconciliationDate: '',
				},
				accts: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.accts = [];
				let reconciliationDate = (!this.filters.reconciliationDate || this.filters.reconciliationDate == '') ? '' : util.formatDate.format(new Date(this.filters.reconciliationDate), 'yyyy/MM/dd');
				let para =  JSON.stringify({
					reconciliationDate: reconciliationDate,
				});
				this.listLoading = true;
				requestQueryAcct(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
					    this.accts = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.accts.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
		  //查看台账详情
			queryAcctDetail: function(index){	
			  let reconciliationDate = (!this.filters.reconciliationDate || this.filters.reconciliationDate == '') ? '' : util.formatDate.format(new Date(this.filters.reconciliationDate), 'yyyy/MM/dd');
				this.$router.push({
				   name: '日总额台账详情',
				   params:{param:this.accts[index],reconciliationDate:reconciliationDate},
			    });
			},
			paramsInit:function(){
				let reconciliationDate = sessionStorage.getItem('reconciliationDate');

					if(reconciliationDate !=null &&reconciliationDate!==""){
						this.filters.reconciliationDate = reconciliationDate;
					}
					sessionStorage.removeItem("reconciliationDate");
					if(reconciliationDate != null){
							this.accts = [];
							this.getInfo();
					}
				
			}
   },
   
   	     mounted: function() {
		   this.paramsInit();
		}
 }	
</script>
<style scoped>

</style>


