<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
			   <el-form-item label="贷款编号" prop="loanNumber">
			    <el-input v-model="filters.loanNumber"  placeholder="请输入贷款编号"></el-input>
				</el-form-item>
				<el-form-item label="冲正原因" prop="rushReason">
			    <el-input v-model="filters.rushReason"  placeholder="请输入冲正原因"></el-input>
				</el-form-item>
				<el-form-item label="冲正日期" prop="rushDate">
					    <el-date-picker v-model="filters.rushDate" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="revTrans" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="loanNumber" label="借款编号" width="300" >
			</el-table-column>
			<el-table-column prop="rushReason" label="冲正原因" width="100" >
			</el-table-column>
			<el-table-column prop="originalTransactionDate" label="原交易日期" width="120" >
			</el-table-column>
			<el-table-column prop="rushDate" label="冲正日期" width="120" >
			</el-table-column>
			<el-table-column prop="rushAmount" label="冲正金额" width="100" >
			</el-table-column>
	        <el-table-column prop="rushPrincipal" label="冲正本金" width="100" >
			</el-table-column>
			 <el-table-column prop="rusInterest" label="冲正利息" width="100" >
			</el-table-column>
			 <el-table-column prop="rushPenaltyInterest" label="冲正罚息" width="100" >
			</el-table-column>
			 <el-table-column prop="rushCost" label="冲销费用" width="100" >
			</el-table-column>				
			<!--<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryAcctFlowDetail(scope.$index)">详情</el-button>
                   <!--<router-link :to="{ name: '资产详情', params: { this.assertData[scope.$index] }}">详情</router-link>-->
          <!--       </template>  
			</el-table-column>-->
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryRevTrans} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					rushDate: '',
					rushReason: '',
					loanNumber: ''
				},
				revTrans: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.revTrans = [];
				let rushDate = (!this.filters.rushDate || this.filters.rushDate == '') ? '' : util.formatDate.format(new Date(this.filters.rushDate), 'yyyy/MM/dd');
				let para =  JSON.stringify({
					rushDate: rushDate,
					rushReason:this.rushDate,
					loanNumber:this.loanNumber
					
				});
				this.listLoading = true;
				requestQueryRevTrans(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
					    this.revTrans = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
					    	let dataObj = JSON.parse(data);
					    	this.revTrans.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},	
   },
   	     mounted: function() {
//		   this.paramsInit();
		}
 }	
</script>
<style scoped>

</style>


