<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters" :rules="queryFormRules">
				<el-form-item label="借款编号" prop="loanNumber">
			    <el-input v-model="filters.loanNumber"  placeholder="请输入贷款编号"></el-input>
				</el-form-item>
				<el-form-item label="交易日期" prop="time">
					    <el-date-picker v-model="filters.time" type="date" placeholder="选择日期时间"></el-date-picker>
				</el-form-item>
				<el-form-item label="备付金账号" prop="bfjNo">
			    <el-input v-model="filters.bfjNo"  placeholder="请输入备付金账号"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getInfo">查询</el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!--列表-->
		<el-table :data="bfjFlows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="bfjNo" label="备付金账号" width="180" >
			</el-table-column>
			<el-table-column prop="transactionDate" label="交易日期" width="120" >
			</el-table-column>
			<el-table-column prop="subject" label="科目" width="80" >
			</el-table-column>
			<el-table-column prop="transactionAmount" label="交易金额" width="100" >
			</el-table-column>
			<el-table-column prop="transactionDesc" label="交易描述" width="200" >
			</el-table-column>
	        <el-table-column prop="loanFlag" label="借贷标记" width="100" >
			</el-table-column>
			 <el-table-column prop="loanNumber" label="借款编号" width="300" >
			</el-table-column>
			 <el-table-column prop="transactionSerialNo" label="交易流水号" width="250" >
			</el-table-column>		
			<!--<el-table-column label="操作" width="100">
				<template scope="scope">
					<el-button size="small" @click="queryAcctFlowDetail(scope.$index)">详情</el-button>
                   <!--<router-link :to="{ name: '资产详情', params: { this.assertData[scope.$index] }}">详情</router-link>-->
          <!--       </template>  
			</el-table-column>-->
		</el-table>
	</section>	
</template>
<script>
	import util from '../../common/js/util'
	import {requestQueryBfjFlow} from '../../api/api';
	export default {
		data() {
			return {
				filters: {
					loanNumber: '',
					time: '',
					bfjNo:''
				},
				bfjFlows: [],
				listLoading: false,
				sels: [],//列表选中列
				queryFormRules: {}
			}
		},
		methods: {
			//获取数据列表
			getInfo() {
				this.bfjFlows = [];
				let transactionDate = (!this.filters.time || this.filters.time == '') ? '' : util.formatDate.format(new Date(this.filters.time), 'yyyy/MM/dd');
				let para =  JSON.stringify({
					loanNumber: this.filters.loanNumber,
					transactionDate: transactionDate,
					bfjNo:this.filters.bfjNo
				});
				this.listLoading = true;
				requestQueryBfjFlow(para).then((res) => {
					let data = res.data;
					if(res.returnCode="000000" && data!=null && data!="" && Array.isArray(JSON.parse(data))){
						let dataObj = JSON.parse(data);
					    this.bfjFlows = dataObj;
					}else if(res.returnCode="000000" && data!=null && data!="" && !Array.isArray(JSON.parse(data))){
							let dataObj = JSON.parse(data);
					    	this.bfjFlows.push(dataObj);
					}else if(res.returnCode!="000000"){
						alert(res.returnMsg);
					}
					this.listLoading = false;
				}).catch((error) => {
				 console.log(error)
				 this.listLoading = false;
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},	
   },
   	     mounted: function() {
//		   this.paramsInit();
		}
 }	
</script>
<style scoped>

</style>


