<template>
    <div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue;">返回</el-button>
		</div>
		<template v-if="details!=null && details!=''">
			<table>
				<tr>
					<td>对账日期：</td><td class="creditPart">{{details.reconciliationDate}}</td>
					<td>上日贷款本金余额：</td><td class="creditPart">{{details.lastLoanPrincipalBalance}}</td>
					<td>当日发放本金总额：</td><td class="creditPart">{{details.dayIssuePrincipalTotal}}</td>
					<td>当日回收本金总额：</td><td class="creditPart">{{details.dayRePrincipalTotal}}</td>
	
				</tr>	
				<tr>
					<td>当日贷款本金余额：</td><td class="creditPart">{{details.dayLoanPrincipalTotal}}</td>
					<td>上日应收利息余额：</td><td class="creditPart">{{details.lastComeInterestBalance}}</td>
					<td>当日计提应收利息：</td><td class="creditPart">{{details.dayPlanComeInterest}}</td>
					<td>当日回收利息：</td><td class="creditPart">{{details.dayComeInterest}}</td>				
				</tr>
				<tr>
					<td>当日应收利息余额：</td><td class="creditPart">{{details.dayComeInterestBalance}}</td>
					<td>上日应收罚息余额：</td><td class="creditPart">{{details.lastComeRenaltyBalance}}</td>
					<td>当日计提应收罚息：</td><td class="creditPart">{{details.dayPlanComeRenalty}}</td>
					<td>当日回收罚息：</td><td class="creditPart">{{details.dayComeRenalty}}</td>
				</tr>
				<tr>
					<td>当日应收罚息余额：</td><td class="creditPart">{{details.dayComeRenaltyBalance}}</td>
					<td>当日冲销本金总额：</td><td class="creditPart">{{details.dayReverseTotal}}</td>
					<td>当日冲销利息：</td><td class="creditPart">{{details.dayReverseInterest}}</td>
					<td>当日冲销罚息：</td><td class="creditPart">{{details.dayReverseRenalty}}</td>
				</tr>
				<tr>
					<td>上日应付正常费用余额：</td><td class="creditPart">{{details.lastComePayNormalBalance}}</td>
					<td>当日应付正常费用：</td><td class="creditPart">{{details.dayComePayNormalCost}}</td>
					<td>当日扣收正常费用：</td><td class="creditPart">{{details.dayBuckleNormalCost}}</td>
					<td>当日应付正常费用余额：</td><td class="creditPart">{{details.dayComePayNormalCostBalance}}</td>
				</tr>	
				<tr>
					<td>当日冲销正常费用：</td><td class="creditPart">{{details.dayReverseNormalCost}}</td>
					<td>上日应付延滞费用余额：</td><td class="creditPart">{{details.lastComePayArrearageCostBalance}}</td>
					<td>当日应付延滞费用：</td><td class="creditPart">{{details.dayComePayArrearageCost}}</td>
					<td>当日扣收延滞费用：</td><td class="creditPart">{{details.dayBuckleArrearageCost}}</td>
				</tr>
				<tr>
					<td>当日应付延滞费用余额：</td><td class="creditPart">{{details.dayComePayArrearageCostBalance}}</td>
					<td>当日冲销延滞费用：</td><td class="creditPart">{{details.dayReverseArrearageCost}}</td>
				</tr>			
			</table>
		</template>	
	</div>	
</template>

<script>
	export default {
		data() {
			return {
					details:''
			}
		},
		methods: {
			//获取数据列表
			getParams:function(){
				if(this.$route.params != null){
				   let reconciliationDate = this.$route.params.reconciliationDate;
				   if(reconciliationDate != null && reconciliationDate!==""){
						sessionStorage.setItem('reconciliationDate', reconciliationDate);
					}
				   this.details = this.$route.params.param;
			}
			},
			back:function(){
				history.go(-1);
			},

        },
	     mounted: function() {
		   this.getParams();
		}
 }
</script>
<style>
		.creditPart{
		color: blue;
		padding-top: 15px;
    	padding-bottom: 15px;
    	text-align:left;
	}
</style>


