<template>
    <div>
		<div class="title">
			<el-button size="middle" @click="back()" class="but1" style="background-color: aliceblue">返回</el-button>
		</div>
		<template v-if="details!=null">
			<div v-for="(item,index) in details">
			<table>
				<tr>
					<td>贷款编号：</td><td class="acctFlowPart">{{item.loanNumber}}</td>
					<td>发生日期：</td><td class="acctFlowPart">{{item.happenDate}}</td>
					<td>币种：</td><td class="acctFlowPart">{{item.currency}}</td>
					<td>交易码：</td><td class="acctFlowPart">{{item.businessCode}}</td>
				</tr>	
				<tr>
					<td>交易描述：</td><td class="acctFlowPart">{{item.businessDesc}}</td>
					<td>借贷标记：</td><td class="acctFlowPart">{{item.loanFlag}}</td>
					<td>入账金额：</td><td class="acctFlowPart">{{item.businessAmount}}</td>
					<td>分支行号：</td><td class="acctFlowPart">{{item.branchNo}}</td>
				</tr>
				<tr>
					<td>产品号：</td><td class="acctFlowPart">{{item.productNo}}</td>
					<td>科目：</td><td class="acctFlowPart">{{item.subject}}</td>
				</tr>	
			</table>
			</div>
		</template>		
	</div>	
</template>

<script>
	export default {
		data() {
			return {
					details:[],
			}
		},
		methods: {

			//获取数据列表
			getParams:function(){
			 let compare = function (obj1, obj2) {
			    let val1 = obj1.happenDate;
			    let val2 = obj2.happenDate;
			    if (val1 < val2) {
			        return -1;
			    } else if (val1 > val2) {
			        return 1;
			    } else {
			        return 0;
			    }            
			}
			sessionStorage.setItem("ass",JSON.stringify(this.$route.params.param));
			sessionStorage.setItem("acc",JSON.stringify(this.$route.params.acctparam));
				if(this.$route.params.acctparam != null){
					let params = this.$route.params.acctparam;			
					params.sort(compare);
					this.details = params;
				}
			},
			back:function(){
				history.go(-1);
			},

        },
	     mounted: function() {
		   this.getParams();
		}
 }
</script>
<style >
	.acctFlowPart{
		color: blue;
		padding-top: 15px;
    	padding-bottom: 15px;
    	text-align:left;
	}
</style>


